.cl {
    &--white {
        color: $white;
        @include heading {
            color: $white; } }
    &--blue {
        color: $blue;
        @include heading {
            color: $blue; } }
    &--darkblue {
        color: $darkBlue;
        @include heading {
            color: $darkBlue; } }
    &--gray {
        color: $gray;
        @include heading {
            color: $gray; } } }
